@import "style/mixins.module.scss";

.root {
    position: absolute;
    left: 8px;
    top: 8px;
    margin: 0;
    z-index: 10;

    @include respond-to("laptop") {
        position: static;
        display: flex;
        align-items: center;
        padding: 6px 8px;
        border: 1px solid get_color("gray", "-4");
        width: fit-content;
    }

    &_round {
        background-color: get_color("primary", "red");
        width: 109px;
        height: 109px;
        top: -20px;
        left: -16px;  
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        border: none;
        background-image: linear-gradient(90.67deg, #FF807D 1.78%, #EB4844 99.68%);

        @include respond-to("laptop") {
            position: absolute;
            width: 165px;
            height: 165px;
            top: -48px;
            left: -48px;
        }
    }
}

.price-value {
    background-color: get_color("primary", "red");
    padding: 6px 8px;
    margin-bottom: 4px;

    @include color("gray", "0");

    @include respond-to("laptop") {
        padding: 0;
        margin-bottom: 0;
        margin-right: 8px;
        background-color: transparent;
        @include color("gray", "-4");
    }

    &_transparent {
        background-color: transparent;
        padding: 0;
        margin: 0;

        @include color("gray", "0");
    }
}

.price-text {
    font-size: 13px;
    line-height: 20px;

    @include color("gray", "0");

    @include respond-to("laptop") {
        font-size: 17px;
        line-height: 24px;
        @include color("gray", "-4");
    }

    &_big {
      font-size: 17px;
      line-height: 24px;

      @include respond-to("laptop") {
        @include color("gray", "0");
      }
  }
}
