@import "style/mixins.module.scss";

.root {
    position: absolute;
    display: none;
    width: 100%;

    @include respond-to("laptop") {
        position: static;
    }

    &_is-active {
        display: block;
    }
}

.video-content {
    width: 100%;
    height: 206px;

    @include respond-to("laptop") {
        height: 365px;
    }
}

.gif,
.video {
    opacity: 0;
    object-fit: cover;
    object-position: center;
    display: none;
    width: 100%;
    height: 100%;

    animation-name: progressImage;
    animation-timing-function: ease-out;
    animation-fill-mode: both;
    animation-duration: 1s;

    &_is-active {
        display: block;
    }

    @include respond-to("laptop") {
        height: 100%;
    }
}

.description {
    font-size: 17px;
    line-height: 24px;
    margin-bottom: 0;

    &_with-label {
        margin-bottom: 8px;
    }
}

.root {
    margin: 0;
}

@keyframes progressImage {
    from {
        display: none;
        opacity: 0;
    }

    to {
        display: block;
        opacity: 1;
    }
}
