@import "style/mixins.module.scss";

.root {
    width: 100%;
    max-width: 100%;
    @include color("gray", "-4");
    margin-bottom: 16px;

    @include respond-to("laptop") {
        margin-bottom: 24px;
    }
}

.topic {
    position: relative;
    display: flex;
    cursor: pointer;
}

.title {
    position: relative;
    margin-bottom: 0;
}

.description {
    overflow: hidden;
    height: 0;
    transition: height 400ms ease;

    &__text {
        margin: 0;
        font-size: 17px;
        line-height: 24px;

        @include respond-to("laptop") {
            font-size: 22px;
            line-height: 32px;
        }
    }
}

.inner {
    opacity: 0;
    transition: opacity 300ms ease;

    &_open {
        opacity: 1;
    }
}
