@import "style/mixins.module.scss";

$image-gap: 8px;

.root {
    position: relative;

    @include respond-to("laptop") {
        padding-left: 40px;
        padding-right: 40px;
    }

    @include respond-to("container-default") {
        width: 1288px;
        margin: 0 auto;
    }
}

.header {
    display: flex;
    flex-direction: column;

    @include respond-to("laptop-L") {
        max-width: 1208px;
        margin: 0 auto;
    }
}

.tabs-content {
    @include respond-to("tablet") {
        padding-left: 40px;
    }

    @include respond-to("laptop") {
        padding: 0;
    }

    @include respond-to("laptop-L") {
        margin-bottom: 53px;
    }
}

.title {
    margin-bottom: 24px;
    padding: 0 24px;

    &_with-subtitle {
        margin-bottom: 8px;
    }

    @include respond-to("tablet") {
        padding-left: 40px;
        padding-right: 40px;
    }

    @include respond-to("laptop") {
        margin-bottom: 32px;
        padding: 0;

        &_with-subtitle {
            margin-bottom: 16px;
        }
    }

    @include respond-to("laptop-L") {
        padding-right: 32px;
        padding-left: 0;
        line-height: 40px;
        white-space: nowrap;
    }
}

.subtitle {
    margin-bottom: 24px;
    padding: 0 24px;
    font-size: 17px;
    line-height: 24px;
    font-weight: 400;

    @include respond-to("tablet") {
        padding-left: 40px;
        padding-right: 40px;
    }

    @include respond-to("laptop") {
        margin-bottom: 32px;
        padding: 0;
    }

    @include respond-to("laptop-L") {
        padding-right: 32px;
        padding-left: 0;
        line-height: 40px;
        white-space: nowrap;
    }
}

.tabs {
    @include respond-to("laptop-L") {
        width: auto;
    }
}

.images {
    display: flex;
    flex-wrap: wrap;

    @include respond-to("laptop") {
        gap: $image-gap;
    }
}

.picture {
    display: inline-flex;
    width: 100%;
    height: 245px;

    &_1 {
        width: 65%;
    }

    &_2 {
        width: 35%;
    }

    &_3 {
        height: 222px;
    }

    @include respond-to("laptop") {
        height: 410px;

        &_1 {
            width: calc(75% - #{$image-gap});
        }

        &_2 {
            width: 25%;
        }

        &_3 {
            height: 444px;
        }
    }
}

.image {
    object-fit: cover;
    display: inline-flex;
    height: 100%;
    width: 100%;
}
