@import "style/mixins.module.scss";

.root {
    position: relative;
    height: 4px;
    width: 100%;
}

.meter {
    display: none;
    height: 100%;
    width: 100%;
    background-color: get_color("primary", "light");
    overflow: hidden;

    &_is-active {
        display: block;
    }
}

.progress-container {
    display: none;
    height: 100%;
    z-index: 0;

    &_is-active {
        display: block;
        z-index: 1;
    }
}

.progress {
    position: absolute;
    height: 100%;
    background-color: get_color("primary", "red");
    animation-name: progressBar;
    animation-timing-function: linear;
    animation-fill-mode: both;
    animation-iteration-count: infinite;
}

@keyframes progressBar {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}
