@import "style/mixins.module.scss";

.title {
    margin-bottom: 40px;
    padding-left: 24px;

    @include respond-to("tablet") {
        padding-right: 40px;
        padding-left: 40px;
    }

    @include respond-to("laptop") {
        padding: 0;
    }
}

.column {
    display: flex;
}

.column-content {
    width: 100%;
    margin-left: -40%;

    @include respond-to("laptop") {
        width: 55%;
        margin-left: 0;
    }
}

.column-images {
    width: 40%;

    @include respond-to("laptop") {
        width: 45%;
    }
}

.content {
    display: flex;
    width: 100%;
    height: 100%;

    @include respond-to("laptop") {
        height: 810px;
    }
}

.video {
    margin-left: 40%;

    @include respond-to("laptop") {
        margin-left: 0;
    }
}

.info {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.description {
    margin-bottom: 40px;
    font-size: 22px;
    line-height: 32px;
}

.info-stages {
    position: relative;
    padding: 0;

    @include respond-to("laptop") {
        flex: 1;
    }
}

.progress {
    position: absolute;
    bottom: 0;
}
